/* unplugin-vue-components disabled */export default defineComponent({
  name: "FilterModelResultManager",
  props: {
    modelFilter: {
      type: Object,
      required: true
    },
    category: {
      type: String,
      required: true
    }
  },
  setup(props) {
    const result = computed(() => props.modelFilter.getFilterList(props.category).getFilterValueStr());
    return {
      result
    };
  }
});